import styled,{css} from 'styled-components';

export const Container = styled.div`
  
    
  height:800px;
 position: relative;
   margin-top:5px;
   position:relative;
   background:white;

   @media(max-width:1185px){
   height:1200px;
}
@media(max-width:800px){
   height:1600px;
}
`;

export const Triangule = styled.div`

   
width: 0;
height: 0;
border-style: solid;
border-width: 600px 0 0 98vw;
border-color: transparent transparent transparent #350455;
position:absolute;
bottom:0;

@media(max-width:1185px){
  
  border-width: 1000px 0 0 98vw;
}


`;

export const SectionName = styled.div`


background:#350455;
 width:50%;
margin-top:13px;

>h1{

  
   
    color:white;

  font-size:55px;
  padding:20px;
  font-family: 'Montserrat', sans-serif;
}
@media(max-width:500px){
    h1{
      font-size:30px;
    }

   }

`;



export const Content = styled.div`
   display:flex;
   flex-direction:row;
   position:absolute;
  flex-wrap:wrap;
   top:200px;
  justify-content:center;
   margin:0 auto;

   
   @media(min-width:1920px){

      top:30%;
     left:14%;
}
    
   
`;

export const Projetos = styled.div`
  margin-left:70px;

  @media(min-width:1920px){

         margin-left:100px;
}   

@media(max-width:1180px){
  
  margin-bottom:50px;
}



`;





export const Listras = styled.div`
    position:absolute;
    display:flex;
    flex-direction:row;

    bottom:0;
    right:0;

`;

const listraCss = css`

 height:1500px;
   width:30px;
   background:#350455;
   margin-right:20px;
   @media(max-width:1240px){


display:none;

}

`;

export const Listra1 = styled.div`${listraCss}`;
export const Listra2 = styled.div`${listraCss}`;

