import styled,{css} from 'styled-components';
import Image from '../../Retrato-Wesley-Web.png'

export const Container = styled.div`
   width:100%;
   height:800px;
   background:pink;
   display:flex;
   position:relative;
   background:#350455;
   align-items:center;
`;

export const First = styled.div`


width: 0;
height: 0;
border-style: solid;
border-width: 325px 762.9px 325px 0;
border-color: transparent white transparent transparent;
position:absolute;
right:0;
`;


export const Content = styled.div`
   background:#E5E5E5;
   border:1px solid black;
   display:flex;
   flex-direction:row;
   justify-content:center;
   align-items:center;
  position: absolute;
  margin-left:40px;
  right:20%;
  top:35%;
  
  @media(max-width:600px){
   right:7%;
   
 }
  

`;

export const Photo = styled.div`
height:300px;
width:300px;
background:url(${Image});
background-size:cover;
border:1px solid black;


`;

export const Description = styled.div`



    width:90%;
    padding:14px;
    >p{

font-size:18px;
font-family: 'Montserrat', sans-serif;
color:black;
line-break:4px;
margin-bottom:5px;
word-spacing:3px;




}
@media(max-width:963px){
    >p{
       font-size:15px;
    }
 }
 @media(max-width:800px){
    >p{
       font-size:13px;
    }
 }
 @media(max-width:600px){
    >p{
       font-size:10px;
    }
   
 }
@media(min-width:1920px){

  >p{

   margin-bottom:30px;
   font-size:22px;
}
}
    
`;

export const SectionName = styled.div`


background:#350455;
 width:50%;
top:0;
position:absolute;
margin-top:13px;

>h1{

  
   
    color:white;
  
  font-size:55px;
  padding:20px;
  font-family: 'Montserrat', sans-serif;
  
}

@media(max-width:900px){
     >h1{
        font-size:40px;
     }
}


`;


export const Listras = styled.div`
    position:absolute;
    display:flex;
    flex-direction:row;

    bottom:0;
    right:0;

`;

const listraCss = css`

   height:800px;
   width:30px;
   background:#350455;
   margin-right:20px;
   @media(max-width:1240px){


display:none;

}

`;

export const Listra1 = styled.div`${listraCss}`;
export const Listra2 = styled.div`${listraCss}`;