import styled ,{css}from 'styled-components';
import {Instagram,Linkedin,Youtube,Soundcloud} from 'styled-icons/boxicons-logos'
import {Bandcamp} from 'styled-icons/fa-brands'
export const Container = styled.div`
    display:flex;
    flex-direction:column;
    height:800px;
    width:100%;
    justify-content:center;
    position:relative;
    
`;

export const SectionName = styled.div`


background:#350455;
 width:30%;
top:0;
position:absolute;


>h1{

  
   
  color:white;
  margin-top:13px;
  font-size:55px;
  padding:20px;
  font-family: 'Montserrat', sans-serif;
}

@media(max-width:850px){

  >h1{
    font-size:40px;
  }
}

@media(max-width:650px){

>h1{
  font-size:30px;
}
}

`;

export const Formulario = styled.div`

    
   position:absolute;
   top:150px;
   justify-content:center;
   display:flex;
   flex-direction:column;
   color:red;
   text-align:end;
 

   >form>input{
        background:#350455;
     width:150px;
     height:80px;
     color:white;
     font-size:30px;
     font-family: 'Montserrat', sans-serif;
     cursor: pointer;
     :hover{
      background:#8e5ead;
     }
   }
   @media(max-width:1100px){
      
    >form> fieldset  >div >input{
      width:600px;
      color:red;
    }

    >form>fieldset>div>textarea{
      width:600px;
      color:red;
    }

   }

   @media(max-width:640px){
      
      >form> fieldset  >div >input{
        width:450px;
        
      }
  
      >form>fieldset>div>textarea{
        width:450px;
        
      }
  
     }

 
   >form > fieldset  >div >input{

    border:1px solid black;
      background:red;
      font-size:20px;
      margin-bottom:50px;
      background:#E5E5E5;
      height:50px;
     
 }

 >form>fieldset>div>textarea{


    border: 1px solid black;
    background:#E5E5E5;
    font-size:20px;
    resize:none;
 }

 @media(min-width:1920px){


  >form  > fieldset >div {

  margin-right:200px;
 
}
}

`;



export const Botao = styled.div`
   position:absolute;
    bottom:-100px;
    right:10px;
    padding:30px;
    width:100px;
    
    background:#350455;
  text-align:center;
  cursor:pointer;
  :hover{

   background:#8e5ead;
  }

  :first-child{
    color:white;
    font-size:30px;
  }

 @media(min-width:1920px){

  left:890px;
 }

 
`;

export const Info = styled.div`

  background:#350455;
  width:100%;
  height: 140px;
  position:absolute;
  bottom:0;

 
 text-align:center;


`;

export const Infos = styled.div`
 
 margin-top:18px;
 position:relative;

 >h2{

color:white;
font-size:28px;
font-weight:900;
font-family: 'Montserrat', sans-serif;
}

>p{
   margin-top:10px;
   color:grey;
}

`;

const iconCss = css`

  height:24px;
  width:24px;
  fill:white;
  margin-left:6px;
  cursor:pointer;
`;

export const Icons = styled.div`
  
  margin-top:15px;

  bottom:0;
  left:500px;

`;

export const Bandcss =css`

   
    color:white;
    height:24px;
    width:24px;
    cursor:pointer;
    margin-left:6px;
`;
export const TwitterIcon = styled(Soundcloud)`${iconCss}`;
export const InstagramIcon = styled(Instagram)`${iconCss}`;
export const LinkedinIcon = styled(Linkedin)`${iconCss}`;
export const SpotifyIcon = styled(Bandcamp)`${Bandcss}`;
export const YoutubeIcon = styled(Youtube)`${iconCss}`;

export const Listras = styled.div`
    position:absolute;
    display:flex;
    flex-direction:row;

    bottom:0;
    right:0;

`;

const listraCss = css`

   height:800px;
   width:30px;
   background:#350455;
   margin-right:20px;
   color:red;
   @media(max-width:1240px){


     display:none;

}




`;

export const Listra1 = styled.div`${listraCss}`;
export const Listra2 = styled.div`${listraCss}`;