import React,{useState} from 'react'
import emailjs from 'emailjs-com'
import axios from 'axios'
import {Container,SectionName,Formulario,Icons,Botao,Info,Infos,SpotifyIcon,YoutubeIcon,
        TwitterIcon,InstagramIcon,LinkedinIcon,Listras,Listra1,Listra2} from './styles.js'
export default function Index() {

  

      

    const [about,setAbout] = useState({

       
            name:'',
            email:'',
            message:'',
            send:false
    })
  
    const handlerName=(e)=>{

       setAbout({
        name:e.target.value,
      } )

    }
    const handlerEmail=(e)=>{

        setAbout({
         email:e.target.value,
       } )
 
     }
     const handlerMessage=(e)=>{

        setAbout({
         message:e.target.value,
       } )
 
     }

    const sendEmail=(e)=> {
        e.preventDefault();
    
        emailjs.sendForm('service_bn1kjyr', 'template_mac64l3', e.target, 'user_RcG0p1ZuUFuR0bKipAwAZ').then((result) => {
              console.log(result.text);
          }, (error) => {
              console.log(error.text);
          });

          e.target.reset();
      }

    

    const  resetForm=()=>{

        setAbout({
            name:'',
            email:'',
            message:'',
           
        })
         
          setTimeout(()=>{
                setAbout({
                    send:false
                })
          },3000)
     }
    return (
        <section id="contato">
        <Container>
            <SectionName>
                <h1>Contato</h1>
            </SectionName>

             <Formulario>
               
            <form className="contact-form" onSubmit={sendEmail}>       

                    <fieldset class="grupo">
                    
                            <div class="campo">
                          
                                <input type="text" name="name" id="nome" value={about.name} onChange={handlerName} size="100" required placeholder="Seu nome"/>
                                
                            </div>
                            
                        
                            <div class="campo">
                              
                                <input type="email" name="email"  id="emailid"  value={about.email}  onChange={handlerEmail} required size="100" placeholder="Seu email"/>
                            </div>
                            <div class="campo">
                             
                                <textarea name="message" id=""  cols="103" rows="7" value={about.message}   onChange={handlerMessage} placeholder="Sua mensagem" ></textarea>
                            </div>

                        </fieldset>  
                        
                           <input type="submit" value="Enviar"/>
                      
              
                
                    
            

                 </form>
                 </Formulario> 

                 
         
                 <Info>
                     <Infos>
                         <h2>Bitdreamer Sound Design</h2>
                         <p>bitdreamersd@gmail.com</p>
                     </Infos>
                     <Icons>
                          < TwitterIcon onClick={()=> window.open(" https://soundcloud.com/bitdreamersx", '_blank')} ></TwitterIcon>
                          <InstagramIcon onClick={()=> window.open("https://www.instagram.com/bitdreamersd/", '_blank')}></InstagramIcon>
                          <LinkedinIcon onClick={()=> window.open("https://www.linkedin.com/in/bitdreamersd/", '_blank')}></LinkedinIcon>
                          <SpotifyIcon onClick={()=> window.open("https://bitdreamersd.bandcamp.com/", '_blank')}></SpotifyIcon>
                          <YoutubeIcon onClick={()=> window.open("https://www.youtube.com/channel/UChQEPbyYRd7tw5qeYF7CSNQ", '_blank')}></YoutubeIcon>
                     </Icons>
                 </Info>

                 <Listras>
                    <Listra1/>
                    <Listra2/>
                </Listras>
        </Container>

        </section>
    )


    
}
